import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { PhotoViewer } from '@ionic-native/photo-viewer';
import { Device } from '@ionic-native/device';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public posts:any;
  constructor(public navCtrl: NavController, private alertCtrl: AlertController, private device: Device,private photoviewer:PhotoViewer,public http: Http) {

    this.http.get('http://ec2-52-66-148-218.ap-south-1.compute.amazonaws.com:8081/regions').map(res => res.json()).subscribe(data => {
        this.posts = data;
        console.log(this.posts);
  });

  }
  
   goToHome(){
    //this.navCtrl.push(HomePage);

     window.history.back();
  }

   openPhotoViewer(){ 
    this.photoviewer.show('https:www.gstatic.com/webp/gallery3/3.png','Optional Title',{share: false});
  }

  deviceDetails(){

      let alert = this.alertCtrl.create({
      title: 'Device Details',
      message: 
      '<span> Model : ' + this.device.model + '</span><br><br>' +
      '<span> Platform : ' + this.device.platform + '</span><br><br>' + 
      '<span> UUID : ' + this.device.uuid + '</span><br><br>',
      
      buttons: ['OK'],
      
    });
    alert.present();
    
  }

 

}



